.header__nav-block p {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  padding-right: 5px;

  &:last-child {
    opacity: 0.4;
  }
}

.header__nav-block {
  display: flex;
  padding-right: 78px;
  padding-left: 78px;
  margin-bottom: 33px;
}

@media (max-width: 1100px) {
  .header__nav-block p {
    padding-right: 0;

    &:last-child {
      max-width: 500px;
    }
  }
}

@media (max-width: $mobile-width) {
  .header__nav-block {
    display: none;
  }
}
