.blog__container {
  padding-right: 78px;
  padding-left: 78px;
  background-image: linear-gradient(to top, #EBEDF0 90%,  #ffffff 10%);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 150px;

  h1 {
    text-align: left;
    width: 100%;
    font-style: normal;
    font-weight: bold;
    font-size: 100px;
    line-height: 120px;
    margin-bottom: 60px;
  }
}

.blog__items-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  margin: 0 auto;
  justify-content: space-between;
}

.blog__link-add-post {
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 60px;
  /* identical to box height, or 273% */
  margin-top: 40px;
  background-color: white;
  border-radius: 52px;
  padding: 10px;
  padding-right: 46px;
  padding-left: 46px;
  color: #333333;
}


.blog__header {
  position: relative;

  h2 {
    max-width: 759px;
  }
}

.blog__header-text {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.blog__bg-link {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    background-image: url("../images/content/arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    width: 16px;
    height: 16px;
    color: white;
    bottom: 72px;
    right: 62px;
    border-radius: 50%;
    padding: 39px;
    border: 1px solid white;
  }
}

.blog__big-item {
  width: 100%;
}

.blog__header-last {
  background-image: url("../images/content/bg__block-header-last.png");
  background-size: cover;
  background-repeat: no-repeat;

    @media 	only screen and (-webkit-min-device-pixel-ratio: 1.5),
    only screen and (min--moz-device-pixel-ratio: 1.5),
    only screen and (-o-min-device-pixel-ratio: 3/2),
    only screen and (min-resolution: $retina-dppx),
    only screen and (min-resolution: $retina-dpi)  {
      background-image: url("../images/content/bg__block-header-last@2x.png");
    }
}

@media (max-width: $tablet-width) {
  .blog__header {
    h2 {
      padding-left: 20px;
      padding-bottom: 20px;
      max-width: 500px;
      font-size: 36px;
    }
  }

  .blog__bg-link {
    &::before {
      right: 14px;
    }
  }

  .blog__items-list {
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
  }
}

@media (max-width: $mobile-width) {
  .blog__mob-title {
    background-color: white;
    padding-top: 17px;
    padding-bottom: 147px;
    margin-bottom: 30px;
  }

  .blog__header {
    margin-bottom: 0;

    h2 {
      padding-left: 20px;
    }
  }

  .blog__bg-link {
    &::before {
      right: 0;
      bottom: -120px;
      left: 30px;
      border: 1px solid black;
      background-image: url("../images/content/arrow-mob.svg");
    }
  }

  .blog__header-text {
    display: flex;
    flex-direction: column;

    h2 {
      padding-left: 20px;
    }
  }

  .blog__container {
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 100px;

    h1 {
      font-style: normal;
      font-weight: bold;
      font-size: 36px;
      line-height: 46px;
      padding-left: 20px;
    }
  }

  .blog__items-list {
    padding-right: 20px;
    padding-left: 20px;
  }

  .blog__big-item-wrap {
    padding-right: 20px;
    padding-left: 20px;
  }

  .blog__mob-title-in {
    background-color: #EBEDF0;
  }

  .blog__header-last {
    background-image: url("../images/content/bg__block-header-last-mob.png");
    background-size: cover;
    background-repeat: no-repeat;

    @media 	only screen and (-webkit-min-device-pixel-ratio: 1.5),
    only screen and (min--moz-device-pixel-ratio: 1.5),
    only screen and (-o-min-device-pixel-ratio: 3/2),
    only screen and (min-resolution: $retina-dppx),
    only screen and (min-resolution: $retina-dpi)  {
      background-image: url("../images/content/bg__block-header-last-mob@2x.png");
    }
  }

  .blog__link-add-post {
    width: 100%;
    max-width: 336px;
    margin-left: 20px;
    margin-right: 20px;
    text-align: center;
  }
}
