.form__container {
  padding-right: 78px;
  padding-left: 78px;
  padding-bottom: 150px;

  h1 {
    font-style: normal;
    font-weight: bold;
    font-size: 100px;
    line-height: 120px;
    /* identical to box height, or 120% */
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: linear-gradient(to right, #FC4A4A 7%, #DC26FA 40%);
    text-transform: uppercase;
  }

  h2 {
    font-style: normal;
    font-weight: 500;
    font-size: 50px;
    line-height: 60px;
    /* or 120% */
    color: rgba(72, 109, 146, 0.6);
    max-width: 849px;
    padding-top: 20px;
  }
}


//
.form__rabs input[type="radio"] {
  display: none;
}

.form__rabs label {
  background: #ffffff;

  color: #999;
  display: inline-block;
  font-size: 16px;
  padding: 15px 40px;
  margin-right: 29px;
  cursor: pointer;
  box-shadow: #ccc 0 0 1px;
  border: 1px solid rgba(72, 109, 146, 0.2);
  box-sizing: border-box;
  border-radius: 52px;

  &:last-child {
    margin-right: 0;
  }
}

.select__item {
  color: #999;
}

.form__rabs input[type="radio"]:checked + label {
  background: #000000;
  color: #ffffff;
  border: 1px solid #000000;
}

.form__rabs input[type="radio"]:checked + label > p {
  color: #ffffff;
}


.form__rabs {
  width: 100%;
  max-width: 1100px;
}

.from__fp-label {
  margin-bottom: 30px;
}

.form__radio {
  padding-top: 80px;
  padding-bottom: 60px;

  h2 {
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    line-height: 50px;
    /* identical to box height, or 125% */
    padding-bottom: 40px;
    color: #000000;
  }
}


.form__rabs-project {
  max-width: 1055px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

//.from__project-label {
//  margin-bottom: 30px;
//}


.form__rabs-project label {
  margin-right: 0;
  display: flex;
  margin-bottom: 30px;
}

.form__texterea-block {
  display: flex;
  flex-direction: column;
  max-width: 987px;
  padding-bottom: 42px;

  h2 {
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    line-height: 50px;
    /* identical to box height, or 125% */

    padding-bottom: 40px;
    color: #000000;
  }

  textarea {
    width: 100%;
    max-width: 987px;
    height: 320px;
    color: rgba(72, 109, 146, 1);
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 60px;
    background: rgba(72, 109, 146, 0.06);
    border: 1px solid rgba(72, 109, 146, 0.2);
    box-sizing: border-box;
    border-radius: 12px;
    padding-top: 30px;
    padding-left: 30px;

    &::placeholder {
      font-style: normal;
      font-weight: normal;
      font-size: 22px;
      line-height: 60px;
      margin: 0;
      padding: 0;
      /* identical to box height, or 273% */
      color: rgba(72, 109, 146, 0.6);
    }
  }
}

.form__file-input {
  display: flex;
  justify-content: flex-end;
  padding-top: 10px;

  label {
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 30px;
    /* identical to box height, or 150% */
    color: rgba(72, 109, 146, 0.5);
    border-bottom: 1px solid rgba(72, 109, 146, 0.2);
    padding-bottom: 6px;
  }

  input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
    text-align: right;
  }
}

.form__input-item {
  display: flex;
  flex-direction: column;

  input {
    background: rgba(72, 109, 146, 0.06);
    border: 1px solid rgba(72, 109, 146, 0.2);
    box-sizing: border-box;
    border-radius: 12px;
    padding: 20px 30px;
    width: 480px;

    &::placeholder {
      font-style: normal;
      font-weight: normal;
      font-size: 22px;
      line-height: 60px;
      /* identical to box height, or 273% */
      color: rgba(72, 109, 146, 0.6);
    }
  }

  label {
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 60px;
    /* identical to box height, or 273% */
    color: #333333;
  }

  &:first-child {
    margin-right: 25px;
  }
}

.form__main-block {
  display: flex;
  padding-bottom: 60px;
}


.form__btn-block {
  display: flex;
  align-items: center;

  button {
    color: white;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
    max-width: 347px;
    background: linear-gradient(94.57deg, #FC4A4A 2.15%, #DC26FA 74.8%), #FFFFFF;
    border-radius: 52px;
  }

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    max-width: 458px;
    padding-left: 30px;
    color: rgba(39, 39, 39, 0.4);

    span {
      margin-left: 5px;
      padding-bottom: 4px;
      border-bottom: 1px solid rgba(72, 109, 146, 0.2);

      a {
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        color: rgba(39, 39, 39, 0.4);
      }
    }
  }
}

.select__current {
  display: none;
}
@media (max-width: 1224px) {
  .form__rabs-project {
    max-width: 1000px;
    align-items: center;
    justify-content: flex-start;

    label {
      margin-right: 30px;
    }
  }
}

@media (max-width: 1124px) {
  .form__container {
    padding-left: 58px;
    padding-right: 58px;
  }

  .form__goals {
    label {
      margin-bottom: 30px;
    }
  }
}

@media (max-width: $tablet-width) {
  .form__main-block {
    flex-direction: column;
  }
}


@media (max-width: $mobile-width) {
  .select {
    position: relative;
    &.is-active {
      .select__body {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        background-color: white;
        position: relative;
        z-index: 100;

        label {
          margin-top: 10px;
          margin-bottom: 10px;
          background: rgba(72, 109, 146, 0.06);
          border: 1px solid rgba(72, 109, 146, 0.2);
          box-sizing: border-box;
          border-radius: 12px;
          width: 100%;
          padding: 15px 30px;
        }
      }
    }
    &__header {
      cursor: pointer;
    }
    &__body {
      display: none;
      left: 0;
      position: absolute;
      top: 100%;
      right: 0;
    }
    &__item {
      cursor: pointer;
    }
  }

  .select__current {
    display: inline-block;
    background: rgba(72, 109, 146, 0.06);
    border: 1px solid rgba(72, 109, 146, 0.2);
    box-sizing: border-box;
    border-radius: 12px;
    width: 100%;
    padding: 15px 30px;
    color: rgba(72, 109, 146, 0.6);
  }

  .form__rabs {
    display: none;
  }

  .form__project {
    padding-bottom: 30px;
  }

  .form__container {
    padding-left: 20px;
    padding-right: 20px;

    h1 {
      font-style: normal;
      font-weight: bold;
      font-size: 36px;
      line-height: 46px;
    }

    h2 {
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 24px;
      /* or 133% */

      max-width: 335px;
      color: rgba(72, 109, 146, 0.6);
    }
  }

  .form__radio {
    padding-top: 60px;
    padding-bottom: 0;

    h2 {
      font-style: normal;
      font-weight: 500;
      font-size: 28px;
      line-height: 36px;
      /* identical to box height, or 129% */
      color: #000000;
      padding-top: 0;
      padding-bottom: 30px;
    }
  }

  .form__texterea-block {
    h2 {
      font-style: normal;
      font-weight: 500;
      font-size: 28px;
      line-height: 36px;
      /* identical to box height, or 129% */
      color: #000000;
      padding-bottom: 20px;
    }
  }

  .form__goals {
    padding-top: 30px;
  }

  .form__main-block {
    input {
      width: 100%;
    }
  }

  .form__input-item {
    &:first-child {
      margin-right: 0;
    }
  }
}

